    box-sizing: border-box;
    height: 100%;
}

*, *:before, *:after {
    box-sizing: inherit;
}

body {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    background-color: var(--platypus);
    height: 100%;
    margin: 0;
}

@font-face {
    font-family: arcadeClassic;
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

@font-face {
    font-family: karmaArcade;
    src: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

:root {
    --platypus: rgb(3, 129, 129);
    --cream: rgb(248, 231, 198);
    --strawberry: rgb(238, 50, 90);
    font-family: arcadeClassic, karmaArcade, system-ui, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

.row,
.board {
    display: grid;
    gap: 5px;
}

.board {
    aspect-ratio: 1;
    grid-template-rows: repeat(10, 1fr);
    cursor: pointer;
}

.row {
    grid-template-columns: repeat(10, 1fr);
}

.disable-board {
    pointer-events: none;
    background-color: rgb(0, 0, 0);
    opacity: .6;
}

.box {
    border: 3px solid var(--cream);
}

.active {
    cursor: crosshair;
}

.not-active {
    cursor: not-allowed;
}

.filled-box {
    background-color: var(--strawberry);
}

.ship-hit {
    background-color: green;
}

.ship-missed {
    background-color: orange;
}

.ship-hit,
.ship-missed {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2em;
    color: var(--cream);
}

.highlight {
    background-color: orange;
}

.page-logo-container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-top: 15px double var(--cream);
    border-bottom: 15px double var(--cream);
}

.page-logo-text {
    font-size: 4rem;
    font-family: karmaArcade;
    color: var(--cream);
    margin: 10px 0;
}

.select-opponent {
    background-color: var(--strawberry);
    padding: 20px 10px;
    width: 60%;
    height: 30%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    outline: 8px dashed orange;
    border-radius: 10px;
    gap: 30px;
    text-align: center;
    margin: auto;
}

.select-opponent,
.place-ship-text {
    font-weight: bold;
    font-size: 2em;
    color: white;
}

.modal-text-container {
    display: flex;
    align-items: end;
}

.select-opponent > div:last-child {
    display: flex;
    gap: 20px;
}

button {
    height: 50px;
    width: 150px;
    background-color: var(--strawberry);
    font-family: arcadeClassic;
    font-size: 1.4rem;
    border: 4px solid var(--cream);
    border-radius: 10px;
    color: white;
    font-weight: bold;
    cursor: pointer;
}

button:hover {
    background-color: var(--cream);
    color: var(--platypus);
    text-decoration: underline;
    border: 4px solid white;
}

.board-text-container {
    width: 30%;
    margin: auto;
}

.place-ships-text-container {
    display: flex;
    color: white;
    align-items: end;
}

.place-ships-text-container,
.rotate-btn {
    margin: 10px 0;
}

.ellipsis {
    width: 30px;
    text-align: left;
    font-family: arcadeClassic;
}

.ellipsis::after {
    overflow: hidden;
    display: inline-block;
    vertical-align: bottom;
    -webkit-animation: ellipsis steps(4,end) 900ms infinite;      
    animation: ellipsis steps(4,end) 900ms infinite;
    content: "\\2026";
    width: 0px;
    font-size: 32px;
}

@keyframes ellipsis {
    to {
        width: 1.25em;    
    }
}

@-webkit-keyframes ellipsis {
    to {
        width: 1.25em;    
    }
}

.all-boards-container {
    display: flex;
    width: 70%;
    gap: 20px;
    margin-top: 40px;
}

.all-boards-container > div {
    flex: 1;
}

.board-title-container {
    display: flex;
    justify-content: center;
    color: var(--strawberry);
    font-size: 1.7rem;
    font-weight: bold;
    margin-bottom: 35px;
}

.board-title {
    background: var(--cream);
    padding: 8px;
    border-radius: 10px;
    outline: 4px solid var(--strawberry);
}

p {
    margin: 0;
}

.game-over-modal,
.pass-device-modal {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
}

.game-over-modal {
    background-color: rgba(0,0,0,0.4);
}

.pass-device-modal {
    background-color: var(--platypus);
}

.game-over-content,
.pass-device-content {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: orange;
    border-radius: 10px;
    padding: 20px;
    width: 50%;
    height: 200px;
    outline: 8px dashed var(--cream);
    gap: 20px;
    color: var(--cream);
    font-weight: bold;
    font-size: 30px;
}

.hide {
    display: none;
}

@media screen and (max-width: 1100px) {
    .board-text-container {
        width: 40%;
    }

    .all-boards-container {
        width: 90%;
        padding-bottom: 20px;
    }

}

@media screen and (max-width: 800px) {
    .page-logo-container {
        margin-bottom: 20px;
    }

    .page-logo-text {
        font-size: 2rem;
    }

    .select-opponent {
        width: 80%;
        font-size: 1.5em;
        gap: 15px;
    }

    .select-opponent > div:last-child {
        flex-direction: column;
    }

    .board-text-container,
    .all-boards-container,
    .game-over-content,
    .pass-device-content {
        width: 90%;
    }

    .board-title-container {
        font-size: 1.2rem;
    }

    .all-boards-container {
        flex-direction: column;
        padding-bottom: 20px;
        gap: 40px;
    }

    .ship-hit,
    .ship-missed {
        font-size: 1.5em;
    }

    .game-over-content,
    .pass-device-content {
        text-align: center;
    }

}

@media screen and (max-width: 400px) {
    .modal-text-container {
        flex-direction: column;
        align-items: center;
    }

    .ellipsis {
        margin-top: -15px;
    }

    .ship-hit,
    .ship-missed {
        font-size: 1em;
    }
}`, "",{"version":3,"sources":["webpack://./src/stylesheets/game-style.css"],"names":[],"mappings":"AAAA;IACI,sBAAsB;IACtB,YAAY;AAChB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,SAAS;IACT,iCAAiC;IACjC,YAAY;IACZ,SAAS;AACb;;AAEA;IACI,0BAA0B;IAC1B,4CAAoC;AACxC;;AAEA;IACI,wBAAwB;IACxB,4CAA0B;AAC9B;;AAEA;IACI,4BAA4B;IAC5B,2BAA2B;IAC3B,8BAA8B;IAC9B,8JAA8J;AAClK;;AAEA;;IAEI,aAAa;IACb,QAAQ;AACZ;;AAEA;IACI,eAAe;IACf,mCAAmC;IACnC,eAAe;AACnB;;AAEA;IACI,sCAAsC;AAC1C;;AAEA;IACI,oBAAoB;IACpB,8BAA8B;IAC9B,WAAW;AACf;;AAEA;IACI,8BAA8B;AAClC;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,mCAAmC;AACvC;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,wBAAwB;AAC5B;;AAEA;;IAEI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,cAAc;IACd,mBAAmB;AACvB;;AAEA;IACI,wBAAwB;AAC5B;;AAEA;IACI,WAAW;IACX,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,oCAAoC;IACpC,uCAAuC;AAC3C;;AAEA;IACI,eAAe;IACf,wBAAwB;IACxB,mBAAmB;IACnB,cAAc;AAClB;;AAEA;IACI,mCAAmC;IACnC,kBAAkB;IAClB,UAAU;IACV,WAAW;IACX,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;IACnB,0BAA0B;IAC1B,mBAAmB;IACnB,SAAS;IACT,kBAAkB;IAClB,YAAY;AAChB;;AAEA;;IAEI,iBAAiB;IACjB,cAAc;IACd,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,SAAS;AACb;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,mCAAmC;IACnC,0BAA0B;IAC1B,iBAAiB;IACjB,8BAA8B;IAC9B,mBAAmB;IACnB,YAAY;IACZ,iBAAiB;IACjB,eAAe;AACnB;;AAEA;IACI,8BAA8B;IAC9B,sBAAsB;IACtB,0BAA0B;IAC1B,uBAAuB;AAC3B;;AAEA;IACI,UAAU;IACV,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,YAAY;IACZ,gBAAgB;AACpB;;AAEA;;IAEI,cAAc;AAClB;;AAEA;IACI,WAAW;IACX,gBAAgB;IAChB,0BAA0B;AAC9B;;AAEA;IACI,gBAAgB;IAChB,qBAAqB;IACrB,sBAAsB;IACtB,uDAAuD;IACvD,+CAA+C;IAC/C,gBAAgB;IAChB,UAAU;IACV,eAAe;AACnB;;AAEA;IACI;QACI,aAAa;IACjB;AACJ;;AAEA;IACI;QACI,aAAa;IACjB;AACJ;;AAEA;IACI,aAAa;IACb,UAAU;IACV,SAAS;IACT,gBAAgB;AACpB;;AAEA;IACI,OAAO;AACX;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,wBAAwB;IACxB,iBAAiB;IACjB,iBAAiB;IACjB,mBAAmB;AACvB;;AAEA;IACI,wBAAwB;IACxB,YAAY;IACZ,mBAAmB;IACnB,oCAAoC;AACxC;;AAEA;IACI,SAAS;AACb;;AAEA;;IAEI,eAAe;IACf,MAAM;IACN,OAAO;IACP,UAAU;IACV,WAA